.RatingValueCheckbox {
  cursor: pointer;
  margin: 4px 0;
  opacity: 0.4; }
  .RatingValueCheckbox label {
    cursor: pointer; }
  .RatingValueCheckbox input[type='checkbox'] {
    display: none; }
  .RatingValueCheckbox:hover, .RatingValueCheckbox--selected {
    opacity: 1; }



